.movies {
    // 1.flex
    // width: 25%;
    // 2.float
    // width: 25%;
    // float: left;
    padding: 20px 15px;
    margin-bottom: 15px;

    // width: 210px;
    img {
        transition: all 250ms ease-in-out;
        border-radius: 4px;
        border: 5px solid #fff;
        max-width: 100%;
        max-height: 100%;
    }

    &:hover img {
        border: 5px solid #6ac045;

    }

    .movies_title {
        color: #fff;
    }

    .movies_year {
        color: #919191;
    }

    .movies_genre {
        color: rgb(247, 140, 140);
    }
    ul {
        list-style: none;
        display: grid;
        text-align: left;
        grid-template-columns: 50% 50%;
        .movies_summary {
            color: #fff;
        }
    }

}
