body {
    .container {
        background: #1d1d1d;
        max-width: 1170px;
        // 1.flex
        // display: flex;
        // flex-wrap: wrap;
        // 2. float
        // overflow: hidden;
        // 3.grid
        display: grid;
        grid-template-columns: 25% 25% 25% 25%;

        margin: 0 auto;
        padding-top: 100px;
        justify-content: space-between;
    }
}


@media screen and (max-width:768px) {
    body {
        .container {
            grid-template-columns: 50% 50%;
        }
    }
}